.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 20vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.productpic {
  width: 150px;  
  height: auto;  
  text-align: center;  
  padding: 15px;
}

.card {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  max-width: 300px;
  margin: auto;
  text-align: center;
  font-family: arial;
}

.price {
  color: grey;
  font-size: 22px;
}

.amount {
  color: grey;
  font-size: 22px;
}

.card button {
  border: none;
  outline: 0;
  padding: 12px;
  color: white;
  background-color: #000;
  text-align: center;
  cursor: pointer;
  width: 100%;
  font-size: 18px;
}

.card button:hover {
  opacity: 0.7;
}

.pPic {
  width: 100%; 
}

.shopbook {
  display: flex;
  flex-wrap: wrap; 
  justify-content: center; 
  justify-content: space-between;
  border: 1px solid black;
}
  
.shopinfo {
  flex: 1 1 0;
  text-align: center; 
  display: flex; 
  flex-direction: column; 
  align-items: center; 
  max-width: 400px
  
}

.summarybook {
  display: flex;
  flex-wrap: wrap; 
  justify-content: center; 
  justify-content: center;
  border: 1px solid black;
  color: aqua;
  
}

.summaryinfo{
  display: flex;
  color: aqua;
}
